import './index.css'

import { QueryClient, QueryClientProvider, ReactQueryDevtools } from '@package/api'
import { UIThemeProvider, I18nextProvider } from '@package/ui'

import { AppRouter } from './AppRouter'
import { i18n } from './i18n'

const queryClient = new QueryClient()

function App(): JSX.Element {
  return (
    <QueryClientProvider client={queryClient}>
      <UIThemeProvider>
        <I18nextProvider i18n={i18n}>
          <AppRouter />
        </I18nextProvider>
      </UIThemeProvider>

      <ReactQueryDevtools initialIsOpen={false} />
    </QueryClientProvider>
  )
}

export default App
